import { mapState, mapMutations, mapActions } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapMutations({
    }),
    ...mapActions([
      'allCollection'
    ]),
    // 播放-暂停
    onTogglePlayingFn (item) {
      const oJson = {
        vid: item.vid || item.id,
        type: 'subjectType',
        page: item.page,
        keywords: '',
        authorId: ''
      }
      this.onSkipPageFn(`/pages/subject/subjectMusic/musicDetails/index?oJson=${JSON.stringify(oJson)}`)
    },
    // 音频收藏
    onCollectionFn (item, status) {
      const data = {
        collectionType: 'subjectType',
        id: item.vid,
        that: this,
        type: 0,
        status
      }
      if (this.userInfo) {
        this.allCollection(data)
      } else {
        this.onSkipPageFn(`/pages/login/index`)
      }
    },
    // 跳转页面
    onSkipPageFn (url) {
      uni.navigateTo({
        url
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    }
  },
  props: {
    lists: {
      type: Array,
      default: []
    }
  }
}
